<template>
    <div class="add-task">
      <h1>Add Task</h1>
      <form @submit.prevent="addTask">
        <div class="form-group">
          <label for="title">Title:</label>
          <input type="text" id="title" v-model="title" placeholder="Enter task title" required />
        </div>
        <div class="form-group">
          <label for="description">Description:</label>
          <textarea id="description" v-model="description" placeholder="Enter task description"
                    style="height: 140px;" required></textarea>
        </div>
        <button type="submit">Add Task</button>
      </form>
    </div>
  </template>
  
  <script>
  import api from '../services/api';
  
  export default {
    data() {
      return {
        title: '',
        description: ''
      };
    },
    methods: {
      async addTask() {
        await api.post('/tasks', {
          title: this.title,
          description: this.description
        });
        this.title = '';
        this.description = '';
        this.$emit('taskAdded');
      }
    }
  };
  </script>
  
  <style scoped>
  .add-task {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  .form-group {
    margin-bottom: 10px;
  }
  
  label {
    font-weight: bold;
    display: block;
  }
  
  input[type="text"],
  textarea,
  button {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ddd;
    border-radius: 3px;
    width: calc(100% - 22px); /* 22px is padding + border width */
    box-sizing: border-box;
    margin-bottom: 10px;
  }
  
  textarea {
    height: 140px;
  }
  
  button {
    background-color: #007bff;
    color: #fff;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  </style>
  