<template>
  <div class="login-form">
    <h2>Login</h2>
    <form @submit.prevent="login">
      <div class="form-group">
        <label for="username">Username:</label>
        <input type="text" id="username" v-model="username" placeholder="Enter your username" required>
      </div>
      <div class="form-group">
        <label for="password">Password:</label>
        <input type="password" id="password" v-model="password" placeholder="Enter your password" required>
      </div>
      <button type="submit">Login</button>
    </form>
  </div>
</template>

<script>
import router from '@/router/index'; // Importa la instancia de Vue Router

export default {
  data() {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
    async login() {
      try {
        // Aquí deberías realizar la lógica de autenticación con tu API
        // Ejemplo simplificado:
        // const response = await api.post('/auth/login', { username: this.username, password: this.password });

        // Simulación de éxito de login
        const response = { data: { token: 'mock-token' } };
        
        // Guarda el token en localStorage o Vuex según tu configuración
        localStorage.setItem('token', response.data.token);

        // Redirige al usuario a la página de creación de tareas
        router.push({ name: 'AddTask' });

      } catch (error) {
        console.error('Login error:', error.response.data.error);
        // Muestra un mensaje de error al usuario
      }
    }
  }
};
</script>

<style scoped>
.login-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 10px;
}

label {
  font-weight: bold;
}

input[type="text"],
input[type="password"],
button {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 3px;
}

button {
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}
</style>
